import { RegisteredWidgetConfig, PropertyOption } from '../../types';

/** å­å¨ window ä¸ä¸ºäºé¿å ng serve å farris rollup æ æ³ä¸è´åä¾æ§ */
declare global {
  interface Window {
    /** åç¬å­ widget base ç propertiesï¼å ä¸ºå®ä¸æ¯ä¸ªåé¨ä»¶ */
    // widgetBaseProperies: PropertyOption[]
    // TODO: èèæ¹é¤ widget name æ¦å¿µ
    registry: { [widgetName: string]: RegisteredWidgetConfig }
  }
}
// if (!window.widgetBaseProperies) window.widgetBaseProperies = []
if (!window.registry) window.registry = {}
/**
 * åé¨ä»¶æ³¨åè¡¨
 */
export const WidgetRegistry = {
  setWidget (name: string, config: RegisteredWidgetConfig) {
    if (!window.registry[name]) window.registry[name] = {}
    window.registry[name] = { ...window.registry[name], ...config }
  },
  getWidgetByName (name: string) {
    return window.registry[name]
  },
  getWidgetByCtorName (ctorName: string) {
    return Object.values(window.registry)
      .find((widgetConfig) => {
        return widgetConfig.ctor.name === ctorName
      })
  }
}
