export * from './preset.jump'
export * from './preset.echarts'

/**
 * dataItem è¿ä¸ªåå­ä¼å½±å exp ä¸­çåæ³ï¼ä¸å¯éææ´æ¹ã
 */
export function resolveTemplate (html: string, dataItem) {
  const expPatt = /{{[\d\D]+?}}/gm
  if (!html) return ''
  const exps = html.match(expPatt) || []
  try {
    exps.forEach(exp => {
      html = html.replace(exp, eval(exp.slice(2, -2)))
    })
  } catch (err) {
    return html
  }
  return html
}

