// let allFuncs
/** èå/èæ¥å¸®å© option */
export const funcOption: PropertyOption = {
  type: PropertyTypes.Help,
  tip: 'éèå/èæ¥çå¸®å©',
  helpOption: {
    getData: (httpClient: HttpClient) => {
      return httpClient
        .get<any[]>('/api/runtime/sys/v1.0/functions/allFuncs')
        .pipe(map(funcs => {
          // allFuncs = funcs
          return list2Tree(funcs, {
            rootParentIdValue: '0',
            beforeProcess (item) {
              if (item.layer === '3') {
                item.isLeaf = true
                item.isTableParent = true
              }
            }
          })
        }))
    },
    structure: HelpStructure.treeTable,
    tableFields: ['code', 'name'],
    valueField: 'id',
    nameField: 'name'
  }
}

