@Injectable({
  providedIn: 'root'
})
export class WidgetPropertyService {

  constructor (
  ) { }
  /**
   * è·å¾æç§ç±»åçå¨å±é»è®¤å¼ï¼å¨å®å¨æ²¡æå¼æ¶ä½¿ç¨
   * @param prop
   * @param arrayItem æ¯å¦æ¯æ¥è·å array çå­é¡¹é»è®¤å¼çï¼é»è®¤ä¸æ¯ã
   * å¦æä¸æ¯ï¼è¥ prop.isArray ä¸ºçï¼å°±è¿åç©ºæ°ç» []ï¼
   * å¦ææ¯ï¼åä¸ç®¡ prop.isArray æ¯å¦ä¸ºçï¼æ»è¿å prop.type å¯¹åºçé»è®¤å¼ï¼ä»åºç¨äºå¢å æ°ç»é¡¹æ¶è·åæ°é¡¹ã
   */
  public getPropertyDefaultValue (prop: PropertyOption, arrayItem = false) {
    if (!arrayItem && prop.isArray) return []
    // object: éå½çæå·æææ key çå¯¹è±¡ï¼æ¯ä¸ª key ç value æ¯é»è®¤å¼
    if (prop.type === PropertyTypes.Object) {
      const newObj = {}
      prop.objectOption.objPropertyOptions.forEach((childProp: PropertyOption) => {
        newObj[childProp.name] = (childProp[childProp.type]
          && childProp[childProp.type].default)
          || this.getPropertyDefaultValue(childProp)
      })
      return newObj
    } else if (prop.type === PropertyTypes.Enum) {
      const firstItem = prop.enumOption.items[0]
      return typeof firstItem === 'string'
        ? firstItem
        : firstItem['value']
    } else if (prop.type === PropertyTypes.Color) {
      const colors = this.getColorsByColorOption(prop.colorOption)
      const i = Math.random()
      return colors[Math.floor(i * colors.length)]
    } else if (prop.type === PropertyTypes.Filter) {
      return new FilterSchema()
    } else {
      return PropertyTypeDefaultValues[prop.type]
    }
  }
  /**
   * åé¨ä»¶å è½½ãç¼è¯å®æåï¼ç»è£æ°ç properties
   * widget name
   * -> constructor name
   * -> widget + title bar + base properties
   * -> åé¨ä»¶çææå¯éç½® properties
   */
  public getWidgetProps (widgetName: string, widgetConfig: WidgetConfig): PropertyOption[] {
    const ctorName = WidgetRegistry.getWidgetByName(widgetName).ctor.name
    // widget å­ç±»ä¸­ç property
    const widgetProps = WidgetPropertyMap.get(ctorName) || []
    // ç­éåº widget å­ç±»ä¸­æ²¡éåè¿ç propertyï¼å¦åå¯è½ä¼æä¿©ä¸æ ·ç
    const baseProps = WidgetPropertyMap.get(WidgetBase.name)
      .filter(baseProp => !widgetProps.some(prop => baseProp.name === prop.name))
    // ç»è£
    return [
      // å¬å±éç½®é½æ¾è¿
      ...baseProps,
      // æ é¢éç½®
      ...(widgetConfig.enableCommonTitleBar
        ? titleBarProps
        : []),
      // ç­ééç½®
      ...(widgetConfig.enableDataConfig && widgetConfig.enableCommonTitleBar
        ? [frontendFilterProp]
        : []),
      // åé¨ä»¶èªèº«éç½®
      ...widgetProps
      // ...testProps,
    ]
  }
  /**
   * mixin å·²æpv + åé¨ä»¶æ¨èçé»è®¤pv + æ­¤ç±»åçå¨å±é»è®¤pv
   */
  public mixinWidgetPropValues (props: PropertyOption[], propValues: object) {
    const mixinedPropValues: { [propName: string]: any } = { ...propValues }
    // æ£æ¥ææ propertiesï¼åªä¸ªæ²¡æå¼ï¼å°±èµä¸é»è®¤æ¨èå¼
    props.forEach(prop => {
      // è¿éå¿é¡»æ¯å¤æ­ undefinedï¼å¦å bool ç±»åç false ä¹å¯è½å¤å®ä¸ºæ å¼
      if (mixinedPropValues[prop.name] === undefined) {
        mixinedPropValues[prop.name] = prop[prop.type] && prop[prop.type].default
          // å¦æåé¨ä»¶æ é»è®¤å¼ï¼å°±å å¥å¨å±çå½åç±»åé»è®¤å¼
          || this.getPropertyDefaultValue(prop)
      }
    })
    return mixinedPropValues
  }

  public getColorsByColorOption (colorOption) {
    if (colorOption && colorOption.palette) return colorOption.palette
    const colorType = colorOption ? colorOption.type : 'pure'
    let colors
    switch (colorType) {
      case 'gradient':
        colors = PlThemeColors.gradient
        break
      case 'pureOrGradient':
        colors = [...PlThemeColors.pure, ...PlThemeColors.gradient]
        break
      case 'pure':
      default:
        colors = PlThemeColors.pure
        break
    }
    return colors
  }

}
