import { Injectable } from '@angular/core';
import { curLang, FILTERS_WIDGET_FILTERS_PROP_NAME, FILTERS_WIDGET_NAME, WidgetEventConfig } from '@gspwidget/util';
import { FilterConfig, FilterSchema } from '../filter';
import { WidgetEventMap } from './widget-event.decorator';
import { WidgetRegistry } from './widget-registry';

@Injectable({
  providedIn: 'root'
})
export class WidgetEventService {

  constructor () { }

  getEventsByWidgetName (widgetName: string, pvs)
    : WidgetEventConfig[] {
    const ctorName = WidgetRegistry.getWidgetByName(widgetName).ctor.name
    const widgetEvents = WidgetEventMap.get(ctorName) || []
    if (widgetName === FILTERS_WIDGET_NAME) {
      return pvs[FILTERS_WIDGET_FILTERS_PROP_NAME].map((fc: FilterSchema) => ({
        name: fc.i18nName[curLang],
        propName: fc.i18nName[curLang],
      }))
    }
    // ç»è£
    return [
      ...widgetEvents,
      ...test,
    ]
  }
}
const test: WidgetEventConfig[] = [{
  name: 'click bar'
}, {
  name: 'event 2'
}, {
  name: 'event 3'
}]
