const splitedGradientColors = PlThemeColors.gradient.map(c => c.split(','))

export const getChartColorsPure = (params) => {
  return PlThemeColors.pure[params.dataIndex];
}
export const getChartColorsGradient = (params) => {
  return new LinearGradient(
    0, 0, 0, 1, [
      {offset: 0, color: splitedGradientColors[params.dataIndex % splitedGradientColors.length][0]},
      {offset: 1, color: splitedGradientColors[params.dataIndex % splitedGradientColors.length][1]}
    ]
  )
}
export const getChartColorsGradientOne = (params) => {
  return new LinearGradient(
    1, 0, 0, 0, [
      {offset: 0, color: splitedGradientColors[params.dataIndex % splitedGradientColors.length][0]},
      {offset: 1, color: splitedGradientColors[params.dataIndex % splitedGradientColors.length][1]}
    ]
  )
}

/**
 * å¯¹äºæ ây è½´èªå®ä¹èå´â ç charts åé¨ä»¶ï¼data é½è¦ç¨å®è¿æ»¤ååç¨
 * @param obj åé¨ä»¶å®ä¾
 */
export function filterYData (obj: { filterYFlag, yFieldObjs: { valueField: DataField, color: string }[], minY, maxY }, data) {
  if (obj.filterYFlag) {
    const yFields = obj.yFieldObjs.map(o => o.valueField.name)
    return data.filter(item => {
      return !yFields.some(yField => item[yField] <= obj.minY || item[yField] >= obj.maxY)
    })
  }
  return data
}

