/**
 * ä¸ç§ä¼ åæ¹å¼ï¼
 * 1. '#000', '#fff'
 * 2. '#000,#fff'
 * 3. '#000'
 */
export function echartsGradient (c1: string, c2?: string) {
  // è¥åªä¼ äºä¸ä¸ªå¼ãè¿ä¸æ¯éå·åå²ï¼åç´æ¥è¿åæ­¤çº¯è²
  if (!c2 && !c1.includes(',')) return c1
  // è¥åªä¼ äºä¸ä¸ªå¼ãæ¯éå·åå²ï¼å°±åå²ä¸ºä¿©é¢è²
  if (!c2) [c1, c2] = c1.split(',')
  return new LinearGradient(
    0, 0, 0, 1, [
      {offset: 0, color: c1},
      {offset: 1, color: c2}
    ]
  )
}

